


#NavBar_1  {
    position: fixed;
    display: flex;
    justify-content: center;
    left: 0;
    right: 0;
    top: 0;
    box-shadow: 0 0 1rem rgba(0, 0, 0, 0.176);
    z-index: 10;
    width: 100vw;
    padding: .5rem 0;
    background-color: white;

    
}

#NavBar_1 .navbar_containt {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 80%;
    padding: .38rem 0;
}








#NavBar_1 .logo_section {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 0;
}




#NavBar_1 .navbar_containt i {
    display: none;
}



#NavBar_1 h3 {
    cursor: default;
    font-size: 1.4rem;
    margin: 0;
}

#NavBar_1 .logo_section h3 span {
    color: var(--main-color);
}

#NavBar_1 ul {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 0rem;
    margin: 0;
}


#NavBar_1 ul div {
    display: flex;
    justify-content: center;
    align-items: center;
}

#NavBar_1 ul div:last-child li {
    background-color: var(--main-color);
    color: white; 
    padding: .3rem 1rem;
    border-radius: .3rem;
}

#NavBar_1 ul div:last-child li a:hover {
    color: black;
}

#NavBar_1 .Link {
    text-decoration: none;
}

#NavBar_1 ul li {
    cursor: pointer;
    list-style-type: none;
    font-size: 1rem;
    transition: .15s;
}

#NavBar_1 ul .contact_btn {
    background-color: var(--main-color);
    border-radius: .2rem;
    color: white;
    padding: .3rem 2rem .4rem 2rem;
    margin-left: 1.5rem;

}

#NavBar_1 ul .contact_btn:hover {
    color: white;
}

#NavBar_1 ul li a {
   color: black;
   text-decoration: none;
   white-space: nowrap;
   /* border: 1px solid red; */
   padding: .3rem .9rem;
}

#NavBar_1 ul a:hover {
    color: var(--main-color);
}



#NavBar_1 i {
    display: none;
    cursor: pointer;
    font-weight: bold;
    font-size: 1.8rem;
    color: black;
    
} 
  
    






@media(max-width: 700px) {
    #NavBar_1.active ul {
        height: 35rem;
        
    }

    #NavBar_1 .navbar_containt {
        display: flex;
        justify-content: center;
        align-items: center;
    }


     
     
     
     #NavBar_1 .logo_section i {
         display: flex;
         align-items: center;
         justify-content: center;
         width: 3rem;
         height: 2.2rem;
         font-size: 2.1rem;
        }


    #NavBar_1 .logo_section img {
        width: 1.5rem;
}
        
        
        #NavBar_1 .navbar_containt {
            flex-direction: column;
            
        }
        
        
        #NavBar_1 ul {
            justify-content: start;
            flex-direction: column;
            gap: 0;
            height: 0rem;
            width: 100%;
            overflow: hidden;
            transition: 1s;
            padding-left: 0;

        }

        #NavBar_1 ul div {
            width: 100%;
        }
        
        
        
        #NavBar_1 ul li {
        text-align: center;
        cursor: pointer;
        width: 100%;
        padding: 1rem 0;
        border-bottom: 1px solid rgba(0, 0, 0, 0.113);
    }

        #NavBar_1 ul li a{
              /* border: 1px solid red; */
              padding: .6rem 30vw;
        }
    
    
        
    #NavBar_1 ul div:last-child li {
        padding: .3rem;
        width: 7rem;
        margin-top: .7rem;
    }


    #NavBar_1 .logo_section {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
    }  


}

